import React, { Component } from 'react';
import styled, { ThemeProvider, injectGlobal } from 'styled-components';
import Header from './Header';
import Meta from './Meta';

const theme = {
  mainColor: '#1a8fff',
  textColor: '#373a42',
  textColorLight: '#ccc',
  borderColor: '#eee',
  bgColor: '#eeecf0',
  neutralColor: '#fff',
  maxWidth: '90vw',
  bs: '0 12px 24px 0 rgba(0, 0, 0, 0.09)',
  offWhite: '#EDEDED',
  red: '#FF0000',
  black: '#373a42',
  grey: '#3A3A3A',
  lightgrey: '#E1E1E1',
  highlighted: '#fff',
};

const StyledPage = styled.div`
  background: white;
  color: ${props => props.theme.black};
`;

const Inner = styled.div`
  max-width: ${props => props.theme.maxWidth};
  margin: 0 auto;
  padding: 2rem;
`;

injectGlobal`
  @font-face {
    font-family: 'radnika_next';
    src: url('/static/radnikanext-medium-webfont.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
  }
  html {
    box-sizing: border-box;
    font-size: .8rem;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }
  body {
    padding: 100;
    margin: 0;
    font-size: 1.5rem;
    line-height: 2;
    font-family: 'radnika_next';
  }
  a {
    text-decoration: none;
    color: ${theme.black};
  }
  button {  font-family: 'radnika_next'; }
`;


class Page extends Component {

  render() {
    return (
        <ThemeProvider theme={theme}>
          <StyledPage>
            <Meta />
            <Header />
            <Inner>
              {this.props.children}
            </Inner>
          </StyledPage>
        </ThemeProvider>
    );
  }
}

export default Page;